<template>
    <div class="col-12 mt-3" v-if="divAvailabilityStatus">
        <div class="col-12 border p-2 float-left" v-if="GetUniqueProductIdData.length > 0">
            <label class="font-weight-bold">{{ $t('Availability') }}</label>
            <div v-if="statusAvailable" class="col-12 p-0">
                <small><i class="text-success fa fa-circle"></i> {{ $t('Available') }}<span
                        id="noOfAvailableItems"></span>-({{ noOfAvailableItems }})</small>
                <div class="col-12 px-0 mt-2" v-if="uniqueItems">
                    <div class="listing">
                        <div class="table-responsive table-fix-header">
                            <table class="table table-bordered dt-responsive nowrap mt-0" id="tblUniqueItems">
                                <thead class="thead-bg">
                                    <tr>
                                        <th class="text-center">
                                            <div class="custom-control custom-checkbox">
                                                <input id="chkAllUniqueItems" class="custom-control-input" type="checkbox"
                                                    name="checkbox" v-model='allSelected' @click="selectAll($event)">
                                                <label class="custom-control-label universal-custom-control-label pt-1"
                                                    for="chkAllUniqueItems"></label>
                                            </div>
                                        </th>
                                        <th class="text-center">{{ $t('AssetId') }}</th>
                                        <th class="text-center">{{ $t('AvailableItems') }}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(item, index) in GetUniqueProductIdData" :key="index">
                                        <td class="text-center">
                                            <div class="custom-control custom-checkbox">
                                                <input :id='item.UNIQUE_ID' class="custom-control-input" type="checkbox"
                                                    name="checkbox" v-model="item.isSelected" :value="item.UNIQUE_ID"
                                                    @change="select($event, item)">
                                                <label class="custom-control-label universal-custom-control-label pt-1"
                                                    :for="item.UNIQUE_ID"></label>
                                            </div>
                                        </td>
                                        <td class="text-center">{{ item.NAME }}</td>
                                        <td class="text-center">{{ item.AVAILABLE_QUANTIY }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="statusReserved">
                <small><i class="text-warning fa fa-circle"></i> {{ $t('Reserved') }} - <span
                        class="text-primary cursor-pointer" id="resvation" @click="select()">{{ $t('ViewReservations')
                        }}</span></small>
                <div class="col-12 px-0" id="divCalendar"></div>
            </div>
            <div v-if="statusUnAvailable">
                <small><i class="text-danger fa fa-circle"></i> {{ $t('Not_Available') }}</small>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        GetUniqueProductIdData: {
            type: Array,
            required: true
        },
        noOfAvailableItems: {
            type: Number,
            required: true
        },
        statusAvailable: {
            type: Boolean,
            required: true
        },
        uniqueItems: {
            type: Boolean,
            required: true
        },
        statusReserved: {
            type: Boolean,
            required: true
        },
        statusUnAvailable: {
            type: Boolean,
            required: true
        },
        divAvailabilityStatus: {
            type: Boolean,
            required: true
        }
    },
    created: function () {
        // console.log(this.GetUniqueProductIdData);
    },
    data() {
        return {
            CheckCatlougeValue: null,
            CheckeduniqueId: '',
            selected: [],
            allSelected: false,
            userIds: []
        }
    },
    methods: {
        selectAll: function (e) {


            var vm = this;
            let productDetails = []
            let passProductIds = '';
            let obj = {};
            vm.allSelected = !vm.allSelected;

            if (vm.allSelected) {

                vm.GetUniqueProductIdData.forEach(ele => {
                    ele.isSelected = true;

                })
            } else {
                vm.GetUniqueProductIdData.forEach(element => {

                    element.isSelected = false;

                })
            }


            vm.GetUniqueProductIdData.forEach(ele => {

                if (ele.isSelected == true) {
                    obj = {
                        productId: ele.UNIQUE_ID,
                        productName: ele.NAME
                    }

                    productDetails.push(obj);
                }
            })
            console.log("Productdetails", productDetails.length);
            passProductIds = productDetails.map(item => item.productId).join(',');
            this.$emit('onUniqueItemClick', passProductIds);


        },


        select: function (event, item) {
            var vm = this;
            let obj = {};
            let productDetails = []
            let passProductIds = '';

            if (event.target.checked == true) {

                vm.GetUniqueProductIdData.forEach(element => {
                    if (element.UNIQUE_ID == item.UNIQUE_ID) {
                        element.isSelected = true;
                        vm.allSelected = false;
                    } else {
                        //
                    }
                })
            }
            else {

                vm.GetUniqueProductIdData.forEach(element => {
                    if (element.UNIQUE_ID == item.UNIQUE_ID) {
                        element.isSelected = false;
                        vm.allSelected = false;
                    } else {
                        //
                    }
                })


            }

            vm.GetUniqueProductIdData.forEach(ele => {

                if (ele.isSelected == true) {
                    obj = {
                        productId: ele.UNIQUE_ID,
                        productName: ele.NAME
                    }

                    productDetails.push(obj);
                }
            })

            if (vm.GetUniqueProductIdData.length == productDetails.length) {
                vm.allSelected = true;
            }
            else {
                vm.allSelected = false;
            }
            passProductIds = productDetails.map(item => item.productId).join(',');
            this.$emit('onUniqueItemClick', passProductIds);

        }
    }
}
</script>