<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header d-flex">
                        <h5 class="modal-title">
                            {{ $t('ProductAvailability') }}
                        </h5>
                        <button v-on:click="ClosePOPuP(event)" type="button" class="close"
                            data-original-title="Close"><em class="fa fa-times"></em></button>
                        <span class="user-guide ">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>User Guide</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('USERGUIDE_AC_CHECK_AVAILABILITY')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div class="modal-body">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">

                            <div class="row">
                                <div class="col-12">
                                    <div class="rad_value" type="radiobutton">
                                        <div class="custom-control custom-radio custom-control-inline">
                                            <input type="radio" class="custom-control-input dynamic IssuedByIds"
                                                id="SearchProductName" @change="searchByProduct('barcode')"
                                                :checked="SearchByBarcode" name="SearchProduct" value="CheckedCheckBox">
                                            <label class="custom-control-label" for="SearchProductName">{{
            $t('SearchbyBarcodeRFID') }}</label>

                                        </div>
                                        <div class="custom-control custom-radio custom-control-inline">
                                            <input type="radio" class="custom-control-input dynamic IssuedByIds"
                                                id="SearchProducts" @change="searchByProduct('product')"
                                                :checked="!SearchByBarcode" name="SearchProduct"
                                                value="CheckedCheckBox">
                                            <label class="custom-control-label" for="SearchProducts">{{
            $t('SearchbyProduct') }}</label>
                                        </div>
                                    </div>
                                </div>
                                <Form ref="BarCodeForm" class="d-block w-100">
                                    <div id="searchByBarcode" class="col-12 mt-3 p-0">
                                        <div class="col-12 col-sm-9 col-md-9 col-lg-10 float-left"
                                            v-if="SearchByBarcode">
                                            <div class="form-group mb-2">
                                                <label><span id="">{{ $t('SearchbyBarcodeRFID') }}</span></label><span
                                                    class="text-danger">*</span>
                                                <Field name="Barcode" :rules="{ 'required': true }" v-slot="{ errors }">
                                                    <input id="txtBarcode" name="Barcode" v-model="txtBarcode"
                                                        placeholder="Enter Barcode"
                                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                        type="text">
                                                    <span v-if="errors[0]" name="Barcode"
                                                        class="invalid-feedback d-block">{{
            errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-12 col-sm-3 col-md-3 col-lg-2 float-left pl-md-0 mt-0 mt-md-4 mt-sm-4"
                                            v-if="SearchByBarcode">
                                            <div class="background-0 text-left w-100 form-check-inline">
                                                <input type="hidden" id="hdnSearch" value="">
                                                <a title="" id="Searchassest_barcode" href="javascript:;"
                                                    v-on:click="SearchassestBarcode"
                                                    class="btn btn-success w-50 mr-1 px-4 hlsearch"
                                                    data-toggle-tooltip="tooltip" data-original-title="Search"><em
                                                        class="fa fa-search"></em></a>
                                                <a id="aClear" href="javascript:;" title="Clear all "
                                                    v-on:click="CleareAssestBarcode"
                                                    class="btn btn-danger w-50 clearsearchtext px-4"
                                                    data-toggle-tooltip="tooltip" data-original-title="Clear"><em
                                                        class="fa fa-refresh"></em></a>
                                            </div>
                                        </div>
                                    </div>
                                </Form>
                                <div class="col-12 float-left searchByProduct" v-if="!SearchByBarcode">
                                    <div class="form-group">
                                        <label for="Subject">{{ $t('Location') }}:<span
                                                class="text-danger">*</span></label>
                                        <Field name="Location" :rules="{ 'required': true }" v-slot="{ errors }">
                                            <tree-view v-model="location_id" :options="options"
                                                v-on:input="GetddlProduct(location_id)" placeholder="Select Location"
                                                :show-count="true"
                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                            </tree-view>
                                            <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
            errors[0] }}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div id="searchByProduct" v-if="!SearchByBarcode" class="col-12 float-left">
                                    <div class="col-12 float-left p-0">
                                        <div class="rad_value" type="radiobutton">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic"
                                                    id="ProductNameItem" @change="searchByItemName('ProductName')"
                                                    :checked="IsProductName" value="ProductName">
                                                <label class="custom-control-label" for="ProductNameItem">{{
            $t('AssetId')
        }}</label>
                                            </div>
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic" id="ItemName"
                                                    @change="searchByItemName('Product')" :checked="!IsProductName"
                                                    value="Product">
                                                <label class="custom-control-label" for="ItemName">{{ $t('ItemName')
                                                    }}</label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-12 float-left mt-3 p-0 productName" v-if="IsProductName">
                                        <div class="form-group">
                                            <label for="Subject">{{ $t('AssetId') }}:<span
                                                    class="text-danger">*</span></label>
                                            <Field name="Product Name" :rules="{ 'required': true }"
                                                v-slot="{ errors }">
                                                <select v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                    name="Product Name" v-model="Product_Id">
                                                    <option value="">Select</option>
                                                    <option v-for="(item, iindex) in productDDLData" :key="iindex"
                                                        v-bind:value="item.value">{{ item.name }}</option>
                                                </select>
                                                <span v-if="errors[0]" name="Product Name"
                                                    class="invalid-feedback d-block">{{
            errors[0] }}</span>
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-12 float-left mt-3 p-0 product" v-if="!IsProductName">
                                        <div class="form-group">
                                            <label>{{ $t('ItemName') }}:<span class="text-danger">*</span></label>
                                            <div class="input-group mb-3" style="position: relative;">
                                                <input id="txtAssetItems" name="txtAssetItems"
                                                    v-on:input="GetAssetUniqueData(location_id)"
                                                    v-model="assetUniqueItem" value="iselectVal"
                                                    placeholder="Enter Product Name" class="form-control" type="text"
                                                    autocomplete="off">
                                                <div class="unique_dynamicdatalist">
                                                    <ul class="listOfProduct" v-if="IsDDLShowHide">
                                                        <li class="listitem all-items"
                                                            v-for="(item, index) in assetUniqueData"
                                                            v-on:click="uniqueItemChange(item)" :key="index">{{
            item.name }}
                                                        </li>
                                                    </ul>
                                                </div>
                                                <a class="btn float-left" href="javascript:;"
                                                    v-on:click="itemDDLShowHide"
                                                    style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;"
                                                    id="customddlinput">
                                                    <em class="dropdown-toggle float-right"></em>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div class="col-12 mb-3 mt-3">
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span>{{ $t('AvailableBetween') }} </span>
                                        </h5>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6 avail-calender">
                                    <div class="input-group">
                                        <label>{{ $t('From') }}:</label><span class="text-danger">*</span>
                                        <v-date-picker @input="onDateChange($event, 'fromdate')" :popover="popover"
                                            :min-date="getMinDate(new Date())" v-bind:name="fromDate"
                                            :dateformat="dateformat" :minute-increment="5" class="timedate  input-group"
                                            v-model="fromDate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input placeholder="M/D/YYYY" v-bind:class="{ 'form-control': true }"
                                                    :value="inputValue" v-on="inputEvents"
                                                    @focus="togglePopoverClick()" />
                                                <div @click="togglePopover()" id="toggles"
                                                    class="input-group-append cal">
                                                    <span class="input-group-text"><em
                                                            class="fa fa-calendar"></em></span>
                                                </div>
                                            </template>
                                        </v-date-picker>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6">
                                    <div class="form-group">
                                        <label>{{ $t('EventStartTime') }}</label>
                                        <div class="input-group">
                                            <v-date-picker mode="Time" class="input-group" v-model="StartTime"
                                                @input="resetAvailabilityStatus" :dateformat="dateformat">
                                                <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                    <input class="form-control" :value="inputValue"
                                                        v-on="inputEvents" />
                                                    <div @click="togglePopover()" class="input-group-append"><span
                                                            class="input-group-text"><em
                                                                class="fa fa-clock-o"></em></span>
                                                    </div>
                                                </template>
                                            </v-date-picker>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6 avail-calender">
                                    <div class="input-group">
                                        <label>{{ $t('Till') }} </label><span class="text-danger">*</span>
                                        <v-date-picker @input="onDateChange($event, 'toDate')" :popover="popover"
                                            :min-date="getMinDate()" v-bind:name="toDate" :minute-increment="5"
                                            class="timedate  input-group" v-model="toDate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input placeholder="M/D/YYYY" v-bind:class="{ 'form-control': true }"
                                                    :value="inputValue" v-on="inputEvents"
                                                    @focus="togglePopoverClick1()" />
                                                <div @click="togglePopover()" id="toggled" class="input-group-append">
                                                    <span class="input-group-text"><em
                                                            class="fa fa-calendar"></em></span>
                                                </div>
                                            </template>
                                        </v-date-picker>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6">
                                    <div class="form-group">
                                        <label>{{ $t('CAEndTime') }}</label>
                                        <div class="input-group">
                                            <v-date-picker mode="Time" class="input-group" v-model="EndTime"
                                                @input="resetAvailabilityStatus">
                                                <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                    <input class="form-control" :value="inputValue"
                                                        v-on="inputEvents" />
                                                    <div @click="togglePopover()" class="input-group-append"><span
                                                            class="input-group-text"><em
                                                                class="fa fa-clock-o"></em></span>
                                                    </div>
                                                </template>
                                            </v-date-picker>
                                        </div>
                                    </div>
                                </div>
                                <CheckBarCodeAvalibility v-if="tblassestgroupinfoIsShow" :BarCodeData="BarCodeData"
                                    :RequestFrom="RequestFrom">
                                </CheckBarCodeAvalibility>
                                <AvailabilityStatus v-if="divAvailabilityStatusIsShow"
                                    :GetUniqueProductIdData="GetUniqueProductIdData"
                                    :noOfAvailableItems="noOfAvailableItems" :statusAvailable="statusAvailable"
                                    :uniqueItems="uniqueItems" :statusReserved="statusReserved"
                                    :statusUnAvailable="statusUnAvailable"
                                    :divAvailabilityStatus="divAvailabilityStatusIsShow"
                                    @onUniqueItemClick="onUniqueItemClick($event)">
                                </AvailabilityStatus>
                                <div class="col-12 mt-3  flex-row-reverse mt-3">
                                    <div class="col-lg-12 text-left">
                                        <div class="py-2">
                                            <span id="spnSelfCheckOutError" class="text-danger"></span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-12 text-right mt-3">
                                    <a href="javascript:;" @click="ProceedToAction('reserve');"
                                        class="btn btn-warning mr-2" v-if="btnReserve">{{
            $t('ProceedtoReserve') }}</a>
                                    <a href="javascript:;" @click="checkAssetAvailability" class="btn btn-success mr-2"
                                        v-if="btnCheckAvailability">{{
                                        $t('Check_Availability') }}</a>
                                    <a href="javascript:;" @click="ProceedToAction('checkout');"
                                        class="btn btn-success mr-2" v-if="btnCheckout">{{
                                        $t('ProceedtoCheckout') }}</a>
                                    <a href="javascript:;" @click="ProceedToAction('assign');"
                                        class="btn btn-primary mr-2" v-if="btnAssign">{{
                                        $t('ProceedtoAssign') }}</a>
                                </div>
                                <div class="col-12 mt-3  flex-row-reverse mt-3">
                                    <div class="col-lg-12 text-left">
                                        <div class="py-2">
                                            <span id="spnSelfCheckOutError" class="text-danger">{{ ErrorText }}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import CheckavailablityDataService from '../../service/DataService'
import CheckBarCodeAvalibility from '../../components/Modal/CheckBarCodeAvalibility.vue'
import AvailabilityStatus from '../../components/Modal/AvailabilityStatus.vue'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    props: {
        CalledFrom: String,
    },
    components: {
        CheckBarCodeAvalibility,
        AvailabilityStatus,
        'tree-view': Treeselect
    },
    data() {
        return {
            productDDLData: [],
            locationDDLData: [],
            assetUniqueData: [],
            checkAvailabilityList: [],
            location_id: null,
            SearchByBarcode: true,
            IsProductName: true,
            IsDDLShowHide: false,
            assetUniqueItem: '',
            txtBarcode: '',
            tblassestgroupinfoIsShow: false,
            divAvailabilityStatusIsShow: false,
            popover: {
                visibility: 'click'
            },
            isLoading: false,
            BarCodeData: [],
            RequestFrom: '',
            IsShowButton: true,
            CheckedCheckBox: 'SearchProductName',
            BarcodeSelected: 'BarCodeSearch',
            StartTime: new Date(),
            EndTime: new Date(),
            UniqueNameListing: 1,
            IsProduct: 'ProductName',
            Product_Id: null,
            fromDate: new Date(),
            toDate: new Date(),
            GetUniqueProductIdData: [],
            SpnSelfCheckOutError: '',
            ErrorText: '',
            ASSETUniqueId: null,
            noOfAvailableItems: null,
            statusAvailable: false,
            uniqueItems: true,
            statusReserved: false,
            statusUnAvailable: false,
            btnCheckAvailability: true,
            btnCheckout: false,
            btnAssign: false,
            btnReserve: false,
            IsDefault: null,
            UserType: null,
            module_id: null,
            GetKeyData: [],
            isUserSelfCheckoutEnabled: false,
            iselectVal: null,
            CheckeduniqueIds: [],
            LocationName: '',
            uniqueProductName: '',
            hdnQuantity: null,
            LocationDataTree: [],
            dateformat: null,
            urlroute: ''
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetddlLocation();
        vm.searchByProduct('barcode');
        vm.fromDate = vm.$options.filters.formatDate(vm.fromDate);
        vm.toDate = vm.$options.filters.formatDate(vm.toDate);
        vm.EndTime = vm.addHoursToDate(vm.EndTime, 1)
        vm.UniqueNameListing = vm.BarcodeSelected != 'BarCodeSearch' ? vm.UniqueNameListing = 1 : vm.UniqueNameListing = 0;
        var UserInfo = vm.GetUserInfo;
        vm.UserId = UserInfo.ID;
        vm.UserName = UserInfo.Name;
        vm.IsDefault = UserInfo.IsDefault;
        vm.UserType = UserInfo.UserType;
        vm.dateformat = UserInfo.DateFormat;
        await vm.GetKey();
    },
    methods: {
        addHoursToDate(date, hours) {
            return new Date(new Date(date).setHours(date.getHours() + hours));
        },
        onDateChange(event, type) {
            var vm = this;
            if (event != null) {
                if (type == 'fromdate') {
                    vm.fromDate = vm.$options.filters.formatDateYear(event);

                } else if (type == 'toDate') {
                    vm.toDate = vm.$options.filters.formatDateYear(event);
                }
            } else {
                // 
            }
        },
        GetKey: async function () {
            var vm = this;
            await vm.GetModuleByName();
            var url = `moduleId=${vm.module_id}&code=USR`;
            await CheckavailablityDataService.GetKeys(url).then(response => {
                if (response.data.length > 0) {
                    vm.GetKeyData = response.data;
                    vm.GetKeyData.filter(item => {
                        if (item.coreConfigKey == 'SelfCheckout' || item.coreConfigKey == 'SelfTransfer') {

                            return vm.isUserSelfCheckoutEnabled = true;
                        }
                    });
                } else {
                    vm.isUserSelfCheckoutEnabled = false;
                }
            });
        },
        GetModuleByName: async function (ths) {
            var url = `moduleName=Asset`;
            await CheckavailablityDataService.GetModuleByName(url).then(response => {
                this.module_id = response.data;
            });
        },
        GetAssetAvailability: async function () {
            var vm = this;
            var assetCatalog = '';
            var assestUniqueId = '';
            var locationId = '';
            if (vm.BarcodeSelected == 'BarCodeSearch') {
                assetCatalog = vm.BarCodeData == '' ? null : vm.BarCodeData[0].AssetCatalogueId;
                assestUniqueId = vm.BarCodeData == '' ? null : vm.BarCodeData[0].ParentUniqueId;
                locationId = vm.BarCodeData == '' ? null : vm.BarCodeData[0].LocationId;
            } else {
                assetCatalog = vm.Product_Id;
                assestUniqueId = vm.iselectVal;
            }
            if (vm.fromDate == null || vm.fromDate == '') {
                vm.fromDate = new Date;
            }
            if (vm.toDate == null || vm.toDate == '') {
                vm.toDate = new Date;
            }

            var assignedFrom = vm.$options.filters.formatDateYear(vm.fromDate);
            var assignedTill = vm.$options.filters.formatDateYear(vm.toDate);
            var startTime = vm.$options.filters.formatTime(vm.StartTime);
            var endTime = vm.$options.filters.formatTime(vm.EndTime);

            var dateTimeValidation = vm.compareDatetime(vm.$options.filters.formatDate(vm.fromDate), vm.$options.filters.formatTimee(vm.StartTime), vm.$options.filters.formatDate(vm.toDate), vm.$options.filters.formatTimee(vm.EndTime));
            if (dateTimeValidation == 1) {
                vm.ShowAlert(vm.$t("From date should be less than or equal To Till date"), "failure", vm.$t('Alert'));
                return false;
            }

            if ((assestUniqueId == null || assestUniqueId == '' || assestUniqueId == 0) && (assetCatalog == null || assetCatalog == '' || assetCatalog == 0)) {
                if ((assestUniqueId == null || assestUniqueId == '' || assestUniqueId == 0)) {
                    vm.ShowAlert(vm.$t("ProductRequired"), "failure", vm.$t('Alert'));
                } else {
                    vm.ShowAlert(vm.$t("InvalidProduct"), "failure", vm.$t('Alert'));
                }
            } else {
                vm.isLoading = true;
                if (vm.BarcodeSelected == 'BarCodeSearch' && vm.UniqueNameListing == 0) {
                    var url = `assetUniqueId=${assetCatalog}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${locationId}&type=ProductName`;
                    await CheckavailablityDataService.GetAssetAvailability(url).then(response => {
                        if (response.data.availabilty != 'ERROR') {
                            if (response.data.availability.length > 0) {
                                if (response.data.availability[0].availabilty == 'AVAILABLE') {
                                    const obj = {
                                        status: 'available',
                                        AvailableItems: response.data.availability[0].availablE_QUANTITY || 0,
                                        IS_SELF_CHECKOUT: response.data.availability[0].iS_SELF_CHECKOUT || false,
                                        UNIQUE_ITEMS: (response.data.availability[0].uniquE_ITEMS && response.data.availability[0].uniquE_ITEMS.length > 0) ? response.data.availability[0].uniquE_ITEMS : ''
                                    };
                                    vm.DisplayAvailability(obj);
                                } else if (response.data.availability[0].availabilty == 'RESERVED') {
                                    var obj = {
                                        status: 'reserved'
                                    };
                                    vm.DisplayAvailability(obj);
                                } else {
                                    vm.DisplayAvailability('unAvailable');
                                }
                            } else {
                                vm.DisplayAvailability('unAvailable');
                            }
                        } else {
                            vm.DisplayAvailability('unAvailable');
                        }
                        vm.isLoading = false;
                    });
                } else {
                    if (vm.IsProduct == 'ProductName') {
                        if (vm.BarcodeSelected == 'BarCodeSearch') {
                            if (vm.location_id == null && vm.location_id == 0) {
                                vm.ShowAlert(vm.$t("CostCentreReq"), "failure", vm.$t('Alert'));
                                vm.isLoading = false;
                                return false;
                            }
                        } else {
                            await vm.GetUniqueProductId();
                            assestUniqueId = vm.ASSETUniqueId;
                        }
                    }
                    if (!(assestUniqueId == null || assestUniqueId == '' || assestUniqueId == 0 || vm.IsProduct == 'Product')) {
                        var url = `assetUniqueId=${assetCatalog}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${vm.location_id}&type=ProductName`;
                        await CheckavailablityDataService.GetAssetAvailability(url).then(response => {
                            if (response.data.availabilty != 'ERROR') {
                                if (response.data.availability.length > 0) {
                                    if (response.data.availability[0].availabilty == 'AVAILABLE') {
                                        const obj = {
                                            status: 'available',
                                            AvailableItems: response.data.availability[0].availablE_QUANTITY || 0,
                                            IS_SELF_CHECKOUT: response.data.availability[0].iS_SELF_CHECKOUT || false,
                                            UNIQUE_ITEMS: (response.data.availability[0].uniquE_ITEMS && response.data.availability[0].uniquE_ITEMS.length > 0) ? response.data.availability[0].uniquE_ITEMS : ''
                                        };
                                        vm.DisplayAvailability(obj);
                                    } else if (response.data.availability[0].availabilty == 'RESERVED') {
                                        var obj = {
                                            status: 'reserved'
                                        };
                                        vm.DisplayAvailability(obj);
                                    } else {
                                        vm.DisplayAvailability('unAvailable');
                                    }
                                } else {
                                    vm.DisplayAvailability('unAvailable');
                                }
                            } else {
                                vm.DisplayAvailability('unAvailable');
                            }
                            vm.isLoading = false;
                        });
                    } else {
                        var url = `assetUniqueId=${assestUniqueId}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${vm.location_id}&type=Product`;
                        await CheckavailablityDataService.GetAssetAvailability(url).then(response => {
                            if (response.data.availabilty != 'ERROR') {
                                if (response.data.availability.length > 0) {
                                    if (response.data.availability[0].availabilty == 'AVAILABLE') {
                                        const obj = {
                                            status: 'available',
                                            AvailableItems: response.data.availability[0].availablE_QUANTITY || 0,
                                            IS_SELF_CHECKOUT: response.data.availability[0].iS_SELF_CHECKOUT || false
                                        };
                                        vm.DisplayAvailability(obj);
                                    } else if (response.data.availability[0].availabilty == 'RESERVED') {
                                        var obj = {
                                            status: 'reserved'
                                        };
                                        vm.DisplayAvailability(obj);
                                    } else {
                                        vm.DisplayAvailability('unAvailable');
                                    }
                                } else {
                                    vm.DisplayAvailability('unAvailable');
                                }
                            } else {
                                vm.DisplayAvailability('unAvailable');
                            }
                            vm.isLoading = false;
                        });
                    }
                }
            }
        },
        compareDatetime: function (dateStart, timeStart, dateEnd, timeEnd) {
            // Get Date objects for start and end datetime
            const startDate = this.concatenateDateAndTime(dateStart, timeStart);
            const endDate = this.concatenateDateAndTime(dateEnd, timeEnd);

            // Compare the Date objects
            //debugger
            if (startDate < endDate) {
                return -1; // Start datetime is before end datetime
            } else if (startDate > endDate) {
                return 1; // Start datetime is after end datetime
            } else {
                return 0; // Datetimes are equal
            }
        },
        concatenateDateAndTime: function (dateStr, timeStr) {
            // Convert date string to Date object
            const dateObj = new Date(dateStr);

            // Extract hours, minutes, and AM/PM from the time string
            const [time, ampm] = timeStr.split(' ');
            let [hours, minutes] = time.split(':').map(part => parseInt(part));

            // Adjust hours for PM time
            if (ampm === 'PM' && hours < 12) {
                hours += 12;
            } else if (ampm === 'AM' && hours === 12) {
                // Midnight (AM) handling
                hours = 0;
            }

            // Ensure hours are within the range 0 to 23
            if (hours < 0) {
                hours = 0;
            } else if (hours > 23) {
                hours = 23;
            }

            // Set the time part of the date object
            dateObj.setHours(hours);
            dateObj.setMinutes(minutes);

            // Return the concatenated datetime
            return dateObj;
        },
        DisplayAvailability: function (availabilty) {
            var vm = this;
            // debugger
            vm.divAvailabilityStatusIsShow = true;
            if (availabilty.status) {
                vm.statusAvailable = true;
                if (availabilty.status == 'available') {
                    if ((vm.IsProduct == "ProductName") && (vm.BarcodeSelected != 'BarCodeSearch' && vm.UniqueNameListing != 1)) {
                        vm.noOfAvailableItems = availabilty.AvailableItems;
                    }
                    if (availabilty.UNIQUE_ITEMS) {

                        vm.GetUniqueProductIdData = JSON.parse(availabilty.UNIQUE_ITEMS);

                        vm.GetUniqueProductIdData.forEach(element => {
                            element.isSelected = false;
                        })

                        if (vm.txtBarcode != '') {
                            var items = [];
                            vm.GetUniqueProductIdData.forEach(function (item) {
                                if (item.UNIQUE_ID == vm.BarCodeData[0].ParentUniqueId) {
                                    items.push(item);
                                }
                            });
                            vm.GetUniqueProductIdData = items;
                        }

                    } else {
                        vm.uniqueItems = true;
                    }
                    var assignedFrom = vm.fromDate;
                    var startTime = vm.StartTime;
                    var startDate = new Date(new Date(Date.parse(assignedFrom)).setHours(0, 0, 0, 0));
                    var startDateTime = assignedFrom + ' ' + startTime.getTime() / 60000;
                    startDateTime = startDateTime.split(' ');
                    startDateTime = startDateTime[1];
                    var date = new Date(new Date().setHours(0, 0, 0, 0));
                    vm.hdnQuantity = availabilty.AvailableItems;
                    vm.statusReserved = false;
                    vm.statusUnAvailable = false;
                    vm.btnCheckAvailability = false;
                    vm.btnCheckout = false;
                    vm.btnAssign = false;
                    if (+startDate === +date) {
                        if (vm.UserType == 'CA' && `${vm.IsDefault}` == 1) {
                            vm.btnCheckout = true;
                            vm.btnAssign = true;
                        } else {
                            if (vm.isUserSelfCheckoutEnabled == 'True') {
                                vm.btnAssign = true;
                            }
                            if (vm.isUserSelfCheckoutEnabled == true && availabilty.IS_SELF_CHECKOUT == true) {
                                vm.btnCheckout = true;
                            } else if (availabilty.IS_SELF_CHECKOUT == false) {
                                vm.ErrorText = 'Self checkout is not enabled for this product.';
                            } else {
                                if (vm.UserType == 'CA') {
                                    vm.ErrorText = 'Please enable your self checkout option under user configuration.';
                                } else {
                                    vm.ErrorText = 'Please ask the company admin to enable your self checkout option under user configuration.';
                                }
                            }
                        }
                        if (startDateTime - (new Date().getTime() / 60000) >= 5) {
                            vm.btnReserve = true;
                        }
                    }
                    if (startDate > date) {
                        vm.btnReserve = true;
                        if (vm.UserType == 'CA' && vm.IsDefault == true) {
                            vm.btnAssign = true;
                        }
                    }
                } else if (availabilty.status == 'reserved') {
                    vm.noOfAvailableItems = null;
                    vm.statusReserved = true;
                    vm.statusUnAvailable = false;
                    vm.btnCheckAvailability = false;
                    vm.btnReserve = false;
                    vm.btnCheckout = false;
                    vm.btnAssign = false;
                }
            } else {
                vm.noOfAvailableItems = null;
                vm.statusReserved = false;
                vm.statusUnAvailable = true;
                vm.btnCheckAvailability = false;
                vm.btnReserve = false;
                vm.btnCheckout = false;
                vm.btnAssign = false;

            }
        },
